// app/districts/page.tsx
'use client';

import { useEffect, useState } from 'react';



interface District {
  id: number;
  name: string;
}

export default function DistrictPage() {
  const [districts, setDistricts] = useState<District[]>([]);
  const [showModal, setShowModal] = useState(false);
  const [districtName, setDistrictName] = useState('');
  const [editingId, setEditingId] = useState<number | null>(null);

  const fetchDistricts = async () => {
    const res = await fetch('https://localhost:7112/api/Districts');
    const data = await res.json();
    setDistricts(data);
  };

  useEffect(() => {
    fetchDistricts();
  }, []);

  const handleSave = async () => {
    if (!districtName.trim()) return alert('Enter district name');
    const method = editingId ? 'PUT' : 'POST';
    const url = editingId
      ? `https://localhost:7112/api/Districts/${editingId}`
      : 'https://localhost:7112/api/Districts';

    await fetch(url, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ id: editingId || 0, name: districtName }),
    });

    setDistrictName('');
    setEditingId(null);
    setShowModal(false);
    fetchDistricts();
  };

  const handleDelete = async (id: number) => {
    await fetch(`https://localhost:7112/api/Districts/${id}`, { method: 'DELETE' });
    fetchDistricts();
  };

  return (
    <div className="p-6">
      <h2 className="text-xl font-bold mb-4">Districts</h2>
      <button onClick={() => { setShowModal(true); setEditingId(null); setDistrictName(''); }} className="bg-blue-600 text-white px-4 py-2 rounded mb-4">➕ Add District</button>
      <table className="w-full border">
        <thead className="bg-gray-200">
          <tr><th className="p-2">ID</th><th className="p-2">Name</th><th className="p-2">Actions</th></tr>
        </thead>
        <tbody>
          {districts.map((d) => (
            <tr key={d.id} className="border-t">
              <td className="p-2">{d.id}</td>
              <td className="p-2">{d.name}</td>
              <td className="p-2 flex gap-2">
                <button onClick={() => { setEditingId(d.id); setDistrictName(d.name); setShowModal(true); }} className="bg-yellow-400 px-2 py-1 rounded">Edit</button>
                <button onClick={() => handleDelete(d.id)} className="bg-red-500 text-white px-2 py-1 rounded">Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {showModal && (
        <div className="fixed inset-0 bg-black/30 flex justify-center items-center">
          <div className="bg-white p-6 rounded-lg w-full max-w-sm">
            <h3 className="text-lg font-semibold mb-4">{editingId ? '✏️ Edit' : '➕ Add'} District</h3>
            <input value={districtName} onChange={(e) => setDistrictName(e.target.value)} className="w-full border px-3 py-2 mb-4 rounded" />
            <div className="flex justify-end gap-2">
              <button onClick={() => setShowModal(false)} className="border px-4 py-2 rounded">Cancel</button>
              <button onClick={handleSave} className="bg-blue-600 text-white px-4 py-2 rounded">Save</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
